import Navbar from "../Navbar"
import { Link } from "react-router-dom"
import { Image } from 'react-bootstrap';

const Main = () => {

    return (
        <>
        <Navbar/>

        <div className="container">
        <br></br>
        <div className="row">
            <div className="col-lg-6 mb-5 col-sm-12">
                <div className="card h-100">
                    <div className="embed-responsive embed-responsive-16by9">
                        <Link to="/dane"> <Image src={require('../../assets/save-data.png')} className="card-img-top " /></Link>
                    </div>
                    <div className="card-block">
                        <br></br>
                        <h4 className="card-title">Pobierz dane</h4>

                        <p className="card-text">Zakadka umoliwiajca pobranie danych odnonie iloci maestw i rozwodw na 1 tys. mieszkacw, a take gstoci zaludnienia w poszczeglnych wojewdztwach ze strony bdl.stat.gov.pl. Pobrane dane mona wywietli z podziaem na lata i wojewdzta, a take zapisa. </p>
                    </div>
                </div>
            </div>
            <div className="col-lg-6 mb-5 col-sm-12">
                <div className="card h-100">
                    <div className="embed-responsive embed-responsive-16by9">
                        <Link to="/zapisane"> <Image src={require('../../assets/view-data.png')}  className="card-img-top " /></Link>
                    </div>
                    <div className="card-block">
                        <br></br>
                        <h4 className="card-title">Zapisane wykresy</h4>

                        <p className="card-text">Zakadka, ktra umoliwia wywietlenie zapisanych przez uytkownika wykresw. Moe je tu ze sob porwna lub usun</p>
                    </div>
                </div>
            </div>
        </div>
    </div>

    </>
    )

}
export default Main
